@import url(https://fonts.googleapis.com/css2?family=Baloo+Bhaijaan+2:wght@500;700&display=swap);
*{box-sizing:border-box}html{font-family:'Baloo Bhaijaan 2', cursive}body,h1,ul{margin:0;padding:0}.App{display:flex;flex-direction:column;align-items:center}.form{display:flex;flex-direction:column;grid-gap:5px;gap:5px;max-width:-webkit-fit-content;max-width:-moz-fit-content;max-width:fit-content}.form__todo{display:block;font-weight:bold;padding:5px 10px}.form__todo:hover{cursor:pointer}.form__select{font-weight:bold;padding:5px 10px}.form__select:hover{cursor:pointer}#error{display:block;background-color:#fa5454;color:#fff;margin:5px 0 0 0;border-radius:10px;padding:5px 10px}.button{opacity:0.7;border:none;padding:5px 10px;background-color:#666;color:#fff;transition:opacity, background-color, border-radius, 0.3s}.button:hover{background-color:lightskyblue;border-radius:10px;opacity:1;cursor:pointer}

.Todo{margin-top:20px}.Todo__list{list-style:none}.Todo__item{position:relative;background-color:wheat;border-radius:10px;padding:20px 80px 5px 30px}.Todo__item:not(:last-child){margin-bottom:10px}.Todo__userId{display:flex;align-items:center;justify-content:center;position:absolute;top:5px;right:5px;height:20px;width:20px;background-color:#fff;border-radius:50%}.Todo__status{position:absolute;top:0;left:10px}.Todo--completed{background-color:springgreen}

